import { PageLink } from '@/components/pagination/pageLink';
import { PaginationProps } from '@/components/pagination/pagination.interface';
import clsx from 'clsx';
import { FC } from 'react';
import { Button, ButtonVariant } from '../button';
export const getPaginationItems = (currentPage: number, lastPage: number, maxLength: number) => {
  const res: Array<number> = [];
  // handle lastPage less than maxLength
  if (lastPage <= maxLength) {
    for (let i = 1; i <= lastPage; i++) {
      res.push(i);
    }
  }
  // handle ellipsis logics
  else {
    const firstPage = 1;
    const confirmedPagesCount = 3;
    const deductedMaxLength = maxLength - confirmedPagesCount;
    const sideLength = deductedMaxLength / 2;
    // handle ellipsis in the middle
    if (currentPage - firstPage < sideLength || lastPage - currentPage < sideLength) {
      for (let j = 1; j <= sideLength + firstPage; j++) {
        res.push(j);
      }
      res.push(NaN);
      for (let k = lastPage - sideLength; k <= lastPage; k++) {
        res.push(k);
      }
    }
    // handle two ellipsis
    else if (currentPage - firstPage >= deductedMaxLength && lastPage - currentPage >= deductedMaxLength) {
      const deductedSideLength = sideLength - 1;
      res.push(1);
      res.push(NaN);
      for (let l = currentPage - deductedSideLength; l <= currentPage + deductedSideLength; l++) {
        res.push(l);
      }
      res.push(NaN);
      res.push(lastPage);
    }
    // handle ellipsis not in the middle
    else {
      const isNearFirstPage = currentPage - firstPage < lastPage - currentPage;
      let remainingLength = maxLength;
      if (isNearFirstPage) {
        for (let m = 1; m <= currentPage + 1; m++) {
          res.push(m);
          remainingLength -= 1;
        }
        res.push(NaN);
        remainingLength -= 1;
        for (let n = lastPage - (remainingLength - 1); n <= lastPage; n++) {
          res.push(n);
        }
      } else {
        for (let o = lastPage; o >= currentPage - 1; o--) {
          res.unshift(o);
          remainingLength -= 1;
        }
        res.unshift(NaN);
        remainingLength -= 1;
        for (let p = remainingLength; p >= 1; p--) {
          res.unshift(p);
        }
      }
    }
  }
  return res;
};
const Pagination: FC<PaginationProps> = ({
  Comp,
  currentPage,
  lastPage,
  maxLength,
  setCurrentPage,
  totalElements,
}: PaginationProps) => {
  const pages = getPaginationItems(currentPage, lastPage, maxLength);
  const itemsPerPage = totalElements / lastPage;
  return (
    <div
        className={clsx(
            'zep-flex',
            'zep-flex-col',
            'sm:zep-gap-1.5',
            'zep-gap-0.5',
            'zep-items-center',
            'zep-text-center')}
        data-testid="zep-pagination"
    >
      <div className={clsx('zep-flex', 'zep-gap-0.5')}>
        <Button className='zep-text-typography-dark-70/70' disabled={currentPage === 1} icon='arrow-long-left' onClick={() => setCurrentPage(currentPage - 1)} variant={ButtonVariant.DarkTertiary}/>
        {pages.map((pageNumber, idx) => (
          <PageLink
            Comp={Comp}
            key={idx}
            active={currentPage === pageNumber}
            disabled={isNaN(pageNumber)}
            onClick={() => setCurrentPage(pageNumber)}
          >
            {!isNaN(pageNumber) ? pageNumber : '...'}
          </PageLink>
        ))}
        <Button className='zep-text-typography-dark-70/70' disabled={currentPage === lastPage} icon='arrow-long-right' onClick={() => setCurrentPage(currentPage + 1)} variant={ButtonVariant.DarkTertiary}/>
      </div>
      <ul className={clsx('zep-flex ', 'zep-gap-0.25')}>
        <li>{itemsPerPage * currentPage - itemsPerPage + 1}</li>
        <li>-</li>
        <li>{itemsPerPage * currentPage}</li>
        <li>von</li>
        <li>{totalElements}</li>
      </ul>
    </div>
  );
};
export { Pagination };
